<template><v-container style="width:400px;height:400px;">

   <v-dialog
      v-model="showmessage"
      width="50%"
      height="75%"
    >
<v-card  elevation="20"   >
        <v-card-title class="headline blue lighten-3 mt-2 mb-2">
            
GAME OVER     
</v-card-title>
          <v-spacer></v-spacer>

        <v-card-text >
             <div class="font-weight-bold mt-8 mb-2 align-centre ">


         <h1 align-middle dense x-large class="text-center"  > {{finalmessage}}
         </h1>

</div>         
        </v-card-text>


        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="clear()"
          >
           Restart
          </v-btn>
          <v-spacer></v-spacer>

        </v-card-actions>
      </v-card>
      </v-dialog>


  <span v-for="index in 9" :key="index">
 
 <!-- WORKING CODE -->
 
 <!-- <v-btn text
      color="success" outlined width="30%" height="30%" x-large :disabled="winnername!=''||cells[index]!=''" :id="index" @click="mark(index)" :value="cells[index]">
     {{cells[index]}}
</v-btn> -->
<v-btn raised
      color="success" outlined width="30%" height="30%"  xx-large :disabled="winnername!=''||cells[index]!=''" :id="index" @click="mark(index)" :value="cells[index]"
      >
     {{cells[index]}}
    
     </v-btn>

  </span>


<!-- 

  clicked {{click}} times and values are
{{cells}}          -->
 </v-container>
</template>

<script>


   export default {
       name:'Ticktac',
   data() {
         return {
            click:0,
            game:1,showmessage:false,
            finalmessage:'',
           cells: {
            1: '', 2: '', 3: '',
            4: '', 5: '', 6: '',
            7: '', 8: '', 9: ''
        },
         winConditions: [
            [1, 2, 3], [4, 5, 6], [7, 8, 9], // rows
            [1, 4, 7], [2, 5, 8],    [3, 6, 9], // columns
            [1, 5, 9], [3, 5, 7]             // diagonals
        ],
        winnername:''
            };
      },
   methods: {
      clear(){
       //  alert('clear entered');
         this.click=1;
         this.game=0;
         this.game=1;
         while(this.click<'10')
         {
   this.cells[this.click]='';                
            this.click+=1;
         }
  this.click=0;
  this.winnername='';
  this.finalmessage='';
  this.showmessage=false;
            }

      
         
         ,
      winner(item){
      item.forEach(element => {
         
         if(this.cells[element[0]]==this.cells[element[1]] && this.cells[element[0]]==this.cells[element[2]] 
         && this.cells[element[0]]!=''){
 //alert('1st value is'+this.cells[element[0]]+ 'second'+this.cells[element[1]]+'third'+this.cells[element[2]])
 this.winnername=this.cells[element[0]];
    this.finalmessage=this.winnername+' is the winner';
    this.showmessage=true;

 //alert("Congratulations "+this.cells[element[0]]+" is the winner");

}

      });
    
     
   }
  ,

            mark(id1) {
         this.click+=1;    
//alert('value is'+id1 + 'and clicked '+this.click);
if(this.click%2==0)
         {
            this.cells[id1]='O';

         }
         else{
            this.cells[id1]='X';
      }

      if(this.click>4)
{
   this.winner(this.winConditions);
   
 
}
if(this.click==9 && this.winnername=='')
{
   this.winnername='draw';
   this.finalmessage='Match is Drawn';
   this.showmessage=true;
}
   }
   }

   }

</script>

<style scoped>


button[disabled=disabled] {
 
background: hsl(140,100%,50%,.2);
  font-size: 50px;
  font-family: cursive;
}



</style>